import React from 'react';
import './Navbar.scss';
import routes from '../../Router/routes';
import { NavLink } from 'react-router-dom';
// import { ReactComponent as SlidtLogo } from './slidt-logo-ny.svg';
import { ReactComponent as FBLogo } from './fb-logo.svg';
import { ReactComponent as IGLogo } from './ig-logo.svg';
import { ReactComponent as MailLogo } from './mail-logo.svg';
import { ReactComponent as PhoneLogo } from './phone-logo.svg';

const Li = props => {
    const { name, exact, path } = props;

    return (
        <li>
            <NavLink to={path} exact={exact}>
                {name}
            </NavLink>
        </li>
    );
}

export default function Navbar(props) {
    return (
        <nav>
            <div className="smlogo">
            <a href="https://www.facebook.com/slidt.dk"><FBLogo className="logos" /></a>
            <a href="https://www.instagram.com/slidt.dk/?hl=da"><IGLogo className="logos" /></a>
            <a href = "mailto: mail@slidt.dk"><MailLogo className="logos" /></a>
            <p className="phone"><PhoneLogo className="logos" />+45 24 40 71 10</p>
            </div>
                {/* <SlidtLogo className="nav-logo"/> */}
            <ul>
                {routes.map((navelement, i) => {
                    console.log(navelement);
                    return (
                        <Li key={navelement.name}
                        {...navelement}
                        />
                        );
                    })}
            </ul>
            <hr className="hr" />
        </nav>
    );
}