import React from 'react';
import './Forside.scss';
import Logo from '../../assets/slidt_svg.svg';

export default function Forside(props) {

    return (
        <div>
            <section>
            <div className="grid-wrapper">
                <div className="mainimg item1"><img className="logoslidt" src={Logo} alt="Logo" /></div>
                <div className="item2 smallimg"><button className="but-sort">Se Sortiment</button></div>
                <div className="item3">
                    <div className="p-main">
                        <h2>Velkommen til Slidt.dk!</h2><br />
                        <p>Showroom - Køb - Salg - Dødsbo - Restbo</p><br />
                        <p>Kontakt: 24 40 71 10</p>
                    </div>
                    </div>
            </div>

                <h2 className="cat-header">Sortiment</h2>
                <hr className="hr" />

            <section className="sortiment">
                <figure className="boxOne images">
                    <figcaption className="picOne pics">Glas</figcaption>
                </figure>
                <figure className="boxTwo images">
                    <figcaption className="picTwo pics">Religiøse Artefakter</figcaption>
                </figure>
                <figure className="boxThree images">
                    <figcaption className="picThree pics">Keramik</figcaption>
                </figure>
                <figure className="boxFour images">
                    <figcaption className="picFour pics">Lamper</figcaption>
                </figure>
                <figure className="boxFive images">
                    <figcaption className="picFive pics">Møbler</figcaption>
                </figure>
                <figure className="boxSix images">
                    <figcaption className="picSix pics">Skilte</figcaption>
                </figure>
                <figure className="boxSeven images">
                    <figcaption className="picSeven pics">Ure</figcaption>
                </figure>
                <figure className="boxEight images">
                    <figcaption className="picEight pics">Malerier</figcaption>
                </figure>
            </section>



            </section>
        </div>
    )
}

